<script>
export default {
  created() {
    this.initErrors();
  },
  props: {
    dialog: {
      type: Object,
      default: function() {
        return {
          show: false
        };
      }
    },
    dialogset: {
      type: Object,
      default: function() {
        return {
          show: false
        };
      }
    },
    dialogcmv: {
      type: Object,
      default: function() {
        return {
          show: false
        };
      }
    },
    form: {
      type: Object,
      default: function() {
        return {
          name: null
        };
      }
    },
    messages: {
      type: Object,
      default: function() {
        return {
          title: "",
          success: "",
          submit: ""
        };
      }
    },
    messagesD: {
      type: Object,
      default: function() {
        return {
          title: "",
          success: "",
          submit: ""
        };
      }
    }
  },
  data: () => ({
    loading: false,
    urlItems: "",
    urlItemsBackground: "",
    reset: false,
    errors: {}
  }),
  watch: {
    form: {
      handler() {
        let self = this;
        this._.each(this.errors, (_error, key) => {
          self.errors[key] = null;
        });
      },
      deep: true
    }
  },
  methods: {
    save_background() {
      this.$store.dispatch("showNotification", {
        statut: false
      });
      let self = this;
      self.$v.$touch();
      if (!self.$v.$invalid) {
        this.loading = true;
        this.$store
          .dispatch("request", {
            url: self.getRouteBackground(),
            method: "POST",
            data: self.getDataFormBackground()
          })
          .then(() => {
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t(self.messages.success)
            });
            self.$emit("getItems");
            self.resetDataForm();
          })
          .catch(error => {
            let message;
            if (error.response.status === 400) {
              message = "$vuetify.error_occured";
              const fields = error.response.data;
              self.setFormErrors(fields);
              this.$forceUpdate();
            } else {
              message = "$vuetify.error_server";
            }
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t(message),
              color: "#B71C1C"
            });
          })
          .then(() => {
            self.loading = false;
          });
      }
      /*else {
               console.log(self.$v);
             }*/
    },
    save() {
      this.$store.dispatch("showNotification", {
        statut: false
      });
      let self = this;
      self.$v.$touch();
      if (!self.$v.$invalid) {
        this.loading = true;
        this.$store
          .dispatch("request", {
            url: self.getRoute(),
            method: self.getMethod(),
            data: self.getDataForm()
          })
          .then(() => {
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t(self.messages.success)
            });
            self.$emit("getItems");
            self.resetDataForm();
          })
          .catch(error => {
            let message;
            if (error.response.status === 400) {
              message = "$vuetify.error_occured";
              const fields = error.response.data;
              self.setFormErrors(fields);
              this.$forceUpdate();
            } else {
              message = "$vuetify.error_server";
            }
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t(message),
              color: "#B71C1C"
            });
          })
          .then(() => {
            self.loading = false;
          });
      }
      /*else {
               console.log(self.$v);
             }*/
    },
    saveHospitals() {
      this.$store.dispatch("showNotification", {
        statut: false
      });
      let self = this;
      self.$v.$touch();
      if (!self.$v.$invalid) {
        this.loading = true;
        this.$store
            .dispatch("request", {
              url: self.getRoute(),
              method: self.getMethod(),
              data: self.getDataForm()
            })
            .then(() => {
              self.$store.dispatch("showNotification", {
                statut: true,
                text: self.$vuetify.lang.t(self.messages.success)
              });
              self.$emit("getItems");
            })
            .catch(error => {
              let message;
              if (error.response.status === 400) {
                message = "$vuetify.error_occured";
                const fields = error.response.data;
                self.setFormErrors(fields);
                this.$forceUpdate();
              } else {
                message = "$vuetify.error_server";
              }
              self.$store.dispatch("showNotification", {
                statut: true,
                text: self.$vuetify.lang.t(message),
                color: "#B71C1C"
              });
            })
            .then(() => {
              self.loading = false;
            });
      }
      /*else {
               console.log(self.$v);
             }*/
    },
    updatePassword() {
      this.$store.dispatch("showNotification", {
        statut: false
      });
      let self = this;
      self.$v.$touch();
      if (!self.$v.$invalid) {
        this.loading = true;
        this.$store
          .dispatch("request", {
            url: self.getRoutePassword(),
            method: "PUT",
            data: self.getDataFormPassword()
          })
          .then(() => {
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t("$vuetify.users.cols.password.success")
            });
            self.$emit("getItems");
          })
          .catch(error => {
            let message;
            if (error.response.status === 400) {
              message = "$vuetify.error_occured";
              const fields = error.response.data;
              self.setFormErrors(fields);
              this.$forceUpdate();
            } else {
              message = "$vuetify.error_server";
            }
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t(message),
              color: "#B71C1C"
            });
          })
          .then(() => {
            self.loading = false;
          });
      }
      /*else {
               console.log(self.$v);
             }*/
    },
    savep() {
      this.$store.dispatch("showNotification", {
        statut: false
      });
      let self = this;
      self.$v.$touch();
      if (!self.$v.$invalid) {
        this.loading = true;
        this.$store
          .dispatch("request", {
            url: self.getRoute(),
            method: "PUT",
            data: self.getDataFormP()
          })
          .then(() => {
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t("$vuetify.users.update.success")
            });
            self.$emit("getItems");
          })
          .catch(error => {
            let message;
            if (error.response.status === 400) {
              message = "$vuetify.error_occured";
              const fields = error.response.data;
              self.setFormErrors(fields);
              this.$forceUpdate();
            } else {
              message = "$vuetify.error_server";
            }
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t(message),
              color: "#B71C1C"
            });
          })
          .then(() => {
            self.loading = false;
          });
      }
      /*else {
               console.log(self.$v);
             }*/
    },
    send() {
      this.$store.dispatch("showNotification", {
        statut: false
      });
      let self = this;
      self.$v.$touch();
      if (!self.$v.$invalid) {
        this.loading = true;
        this.$store
          .dispatch("request", {
            url: self.getRouteBuyingPrice(),
            method: "POST",
            data: self.getDataForm()
          })
          .then(() => {
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t(self.messages.success)
            });
            self.$emit("getItems");
            self.resetDataForm();
          })
          .catch(error => {
            let message;
            if (error.response.status === 400) {
              message = "$vuetify.error_occured";
              const fields = error.response.data;
              self.setFormErrors(fields);
              this.$forceUpdate();
            } else {
              message = "$vuetify.error_server";
            }
            self.$store.dispatch("showNotification", {
              statut: true,
              text: self.$vuetify.lang.t(message),
              color: "#B71C1C"
            });
          })
          .then(() => {
            self.loading = false;
          });
      }
      /*else {
               console.log(self.$v);
             }*/
    },
    getRoute() {
      if (this.form.id) {
        return this.urlItems + "/" + this.form.id;
      } else {
        return this.urlItems;
      }
    },
    getRouteBackground() {
      if (this.form.id) {
        return this.urlItemsBackground + "/" + this.form.id;
      } else {
        return this.urlItemsBackground;
      }
    },
    getRoutePassword() {
      if (this.form.id) {
        return this.urlItemsPassword + "/" + this.form.id;
      } else {
        return this.urlItemsPassword;
      }
    },
    getMethod() {
      //this.reset = this.form.id === undefined
      return this.form.id ? "PUT" : "POST";
    },
    getDataForm() {
      return this.form;
    },
    getDataFormBackground() {
      return this.form;
    },
    initErrors() {
      this._.each(this.form, (value, key) => {
        this.errors[key] = null;
      });
    },
    setFormErrors(errors) {
      let self = this;
      this._.each(errors, (_error, key) => {
        if (self._.hasIn(self.errors, key)) {
          self.errors[key] = _error;
          //console.log("back error", errors[key]);
        }
      });
    },
    resetDataForm() {
      this.form.id = null;
      if (this.form.id) return;
      let self = this;
      this.$v.form.$reset();
      this._.each(this.form, (field, k) => {
        if (typeof self.form[k] === "object") {
          self._.each(self.form[k], (subField, i) => {
            if (typeof self.form[k][i] === "boolean") {
              self.form[k][i] = false;
            } else {
              self.form[k][i] = null;
            }
          });
        } else {
          if (typeof self.form[k] === "boolean") {
            self.form[k] = false;
          } else {
            self.form[k] = null;
          }
        }
      });
    }
  }
};
</script>
